import React, { Component } from 'react';
import { UnControlled as CodeMirror } from 'react-codemirror2';

type ConfigProps = {

};

type ConfigState = {
    example: number;
};

export const ConfigExampleCLI = `$ atomicswap --help
$ A fast and easy way to do atomic swaps for Divi and Bitcoin. Learn more at https://github.com/DiviProject/atomic-swaps
$
$ Usage:
$    atomicswap [flags]
$    atomicswap [command]
$
$ Available Commands:
$    audit       audit and validate an existing atomic swap contract
$    extract     extract the secret for an atomic swap
$    help        Help about any command
$    initiate    initiate an atomic swap
$    participate participate in an atomic swap
$    redeem      redeem an atomic swap
$    refund      refund an atomic swap
$    version     print the version of atomicswap
$ Flags:
$    -c, --config string         the path to the util.Configuration file (default ".atomicswap.json")
$        --grpc-port int         the port of the gRPC server (raw tcp) (default 9999)
$    -h, --help                  help for atomicswap
$    -m, --mainnet               use the mainnet Configuration (default true)
$    -r, --regtest               use the regtest Configuration
$        --rpc-host string       the host of the wallet RPC server (default "localhost")
$    -p, --rpc-password string   the password of the wallet RPC server (default "password")
$        --rpc-port int          the port of the wallet RPC server (default 1337)
$    -u, --rpc-user string       the user of the wallet RPC server (default "user")
$    -s, --server                run as a gRPC server
$        --server-port int       the port of the HTTP server (default 9001)
$    -t, --testnet               use the testnet Configuration
$
$ Use "atomicswap [command] --help" for more information about a command.`;

export const ConfigExampleJSON = `{
    "rpc-host": "localhost",
    "rpc-port": 1337,
    "rpc-user": "divi",
    "rpc-password": "divi",
    "base-currency": "divi",
    "swap-currency": "bitcoin",
    "mainnet": false,
    "testnet": false,
    "regtest": true,
    "server": false,
    "server-port": 9001,
    "grpc-port": 9999
}`;

export class ConfigExample extends Component<ConfigProps, ConfigState> {
    public constructor(props: any) {
        super(props);
        this.state = { example: 0 };
    }

    public render() {
        return(
            <div className="example-wrap" id="e1">
                <h2>Configuring for Atomic Swaps</h2>
                <div className="example">
                    <div className="example-buttons">
                        <a className={`example-button ${this.state.example === 0 ? 'active' : ''}`} onClick={(() => this.setState({ example: 0 }))}>
                            CLI
                        </a>
                        <a className={`example-button ${this.state.example === 1 ? 'active' : ''}`} onClick={(() => this.setState({ example: 1 }))}>
                            .atomicswap.json
                        </a>
                    </div>

                    <div className="example-content">
                        <div className="example-text">
                            <p>
                                In order to configure an atomic swap server (or command line tool).
                                You will need to either pass arguments into the command line or use an
                                atomic swap json configuration file.
                            </p>
                            <p>
                                Below showcases how you can configure atomic swaps with both a configuration
                                file and via command line flags.
                            </p>
                        </div>
                        <CodeMirror
                            className="larger"
                            value={this.state.example === 0 ? ConfigExampleCLI : ConfigExampleJSON}
                            options={{
                                mode: 'shell',
                                theme: 'material',
                                lineNumbers: true
                            }}
                        />
                    </div>
                </div>
            </div>
        );
    }
}
