import React, { Component } from 'react';
import { UnControlled as CodeMirror } from 'react-codemirror2';

export class ExecutionExample extends Component {
    public constructor(props: any) {
        super(props);
        this.state = { menu: false };
    }

    public render() {
        return(
            <div className="example-wrap" id="e3">
                <h2>Executing Commands</h2>
                <div className="example">
                    <div className="example-content">
                        <div className="example-text">
                            <p>
                                When executing commands via the command line. You will get outputs that
                                are required to do other functions with the atomic swap contract.
                                Namely contract transactions, secret seed for the atomic swap
                                and so forth.
                            </p>
                            <p>
                                You will need to copy and paste the output from the command line output in order to do this.
                                If you are using HTTP. You can reference the API instead from the API Reference page.
                            </p>
                        </div>
                        <CodeMirror
                            value='$ atomicswap initiate [address] [amunt]
$ # Output with secret seed, transaction hash etc.
$ atomicswap participate [address] [amount] [secret]
$ ...'
                            options={{
                                mode: 'shell',
                                theme: 'material',
                                lineNumbers: true
                            }}
                        />
                    </div>
                </div>
            </div>
        );
    }
}
